<template>
  <div id="app">
    <keep-alive>
        <router-view/>
    </keep-alive>
    <tabbar v-model="active">
      <tabbar-item icon="exchange" @click="goDataTracking">数据追踪</tabbar-item>
      <tabbar-item v-if="showBoss" icon="edit-data" @click="goBoss">Boss雷达</tabbar-item>
      <tabbar-item icon="contact" @click="goMe">我</tabbar-item>
    </tabbar>
  </div>
</template>

<script>
import { Tabbar, TabbarItem } from "vant";
import { setItem, getItem } from "./util/util";
import { mapState, mapGetters } from "vuex";
export default {
    name: "App",
    data() {
        return {
            showBoss: true
        };
    },
    created() {
        this.windowReload();
        // 将名片id存在本地
        let cardId = document
            .getElementById("hide-input")
            .getAttribute("value");
        if (cardId) {
            setItem("cardId", cardId);
        }
        // 将app_id存在本地
        let appId = document.getElementById("app-id").getAttribute("value");
        if (appId) {
            setItem("appId", appId);
        }
        //显示boss雷达
        let showBoss = document.getElementById("is-boss").getAttribute("value");
        if (showBoss == 0) {
            this.showBoss = false;
        }
    },
    mounted() {
        // 适配x
        let isIphoneX =
            window.devicePixelRatio &&
            window.devicePixelRatio === 3 &&
            window.screen.width === 375 &&
            testUA("iPhone");
        if (isIphoneX) {
            document.body.classList.add("fix-iphonex-bottom");
        }
        function testUA(str) {
            return navigator.userAgent.indexOf(str) > -1;
        }
    },
    methods: {
        goDataTracking() {
            let path = getItem("path");
            if (path) {
                this.$router.push(`${path}`);
            } else {
                this.$router.push("/data-tracking/time");
            }
            document.title = "数据追踪";
        },
        goBoss() {
            this.$router.push("/boss");
            document.title = "Boss雷达";
        },
        goMe() {
            this.$router.push("/me");
            document.title = "我的名片";
        },
        windowReload() {
            window.onload = () => {
                setItem("path", "/data-tracking/time");
            };
        }
    },
    computed: {
        ...mapState({
            childrenRoute: state => state.childrenRoute
        }),
        active: {
            get() {
                if (this.$route.fullPath === "/me") {
                    return 2;
                } else if (this.$route.fullPath === "/boss") {
                    return 1;
                } else {
                    return 0;
                }
            },
            set() {}
        }
    },
    components: {
        Tabbar,
        TabbarItem
    }
};
</script>

<style>
body {
    padding-bottom: 55px;
    background-color: #f5f5f5;
}
.van-tab--active {
    color: #38f !important;
}
.van-tabs__line {
    background-color: #38f !important;
}
.van-cell__title {
    flex: 1;
}
.van-cell__value {
    flex: 2 !important;
}
.me .main .van-cell::after {
    border-color: #ffffff;
}
.fix-iphonex-bottom {
    padding-bottom: 89px;
}
.fix-iphonex-bottom .van-tabbar--fixed {
    padding-bottom: 34px;
}
.van-tabbar--fixed {
    right: 0;
}
.fix-iphonex-bottom #app {
    min-height: 700px;
}
/* .fix-iphonex-bottom .router-content{
  bottom: 85px;
}
.router-content{
  position: fixed;
  top: 44px;
  right: 0;
  bottom: 50px;
  left: 0;
  overflow-y: auto;
} */
</style>



