import Vue from 'vue';
import VueRouter from 'vue-router';

// 数据追踪
const DataTracking    = () => import('@/pages/DataTracking/DataTracking');
const Time            = () => import('@/pages/Time/Time');
const Actions         = () => import('@/pages/Actions/Actions');
const People          = () => import('@/pages/People/People');
// boss雷达
const Boss            = () => import('@/pages/Boss/Boss');
// 我的
const Me              = () => import('@/pages/Me/Me');
const CodeImg         = () => import('@/pages/CodeImg/CodeImg');


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/data-tracking/time'
  },
  {
    path: '/data-tracking',
    name: 'DataTracking',
    component: DataTracking,
    children: [
      {
        path: 'time',
        name: 'Time',
        component: Time
      },
      {
        path: 'actions',
        name: 'Actions',
        component: Actions
      },
      {
        path: 'people',
        name: 'People',
        component: People
      }
    ]
  },
  {
    path: '/boss',
    name: 'Boss',
    component: Boss
  },
  {
    path: '/me',
    name: 'Me',
    component: Me
  },
  {
    path: '/codeimg',
    name: 'CodeImg',
    component: CodeImg
  }
]

const router =  new VueRouter({
  mode: 'hash',
  routes
})

// router.beforeEach((to, from, next) => {
//     if((from.fullPath === '/me' || from.fullPath === '/boss') && to.path !== '/codeimg' ) {
//         let urls = ['/data-tracking/time', '/data-tracking/actions', '/data-tracking/people'];
//         next({
//           path: `${this.childrenRoute}`
//         })
//     }else{
//       next();
//     }
// })

export default router



